.detail{
    width: 1200px;
    margin: 0 auto;
    padding-top: 80px;

    overflow: hidden;
}
.detail .title{
    margin-left: 100px;
    margin-bottom: 100px;
}
.cart{
  width: 700px;

    height: 500px;
}
.cart h1{
    padding: 20px 0;
}
.cart img{
   width:700px ;
    height:500px ;
}
.cart_right{
    width: 488px;
    height: 500px;

    padding-top: 50px;
    font-size: 18px;
}
.detail div{
    float:left;
}
.cart_right p{
    padding-left: 50px;
}

.cart_right p:first-child span:nth-child(2){
    color: darkred;
    font-size: 30px;
}
.cart_right p:nth-child(2) s{
    color: #ccc;
}
.cart_right p:nth-child(5){
    display: flex;
    justify-content: space-between;
}
.cart_right p:nth-child(3){

}
.cart_right p:nth-child(3){
    display: flex;
    align-items: center;
}
.cart_right p:nth-child(3) span{
    margin: 0 5px;
}
.cart_right p:nth-child(4){
    display: flex;
    justify-content: flex-start;
}
.cart_right p:nth-child(4) span{
    padding: 0 7px;
}