.titl{
    width: 1200px;
    margin: 0 auto;
    padding-top: 30px;
    background: #f7f7f7;

}
.titl p{
    font-size: 13px;

}
.search{
    width: 1200px;
    margin: 0 auto;
    margin-top: 10px;
    background: #ccc;
}
.search ul{
    position: absolute;
    background: #fff;
    width: 1200px;
    margin: 0 auto;
}
 .bigpic{
    width: 1200px;
    margin: 50px auto;

}
.bigpic img{
   margin-left: 40px;
}
.smallpic{
    width: 1200px;
    margin: 0 auto;

    display: flex;
    justify-content: space-around;
    align-items: center;
}

.smallpictitle{
    width: 1200px;
    margin: 0 auto;
}
.smallpic img{
    width: 200px;
    height: 150px;

}
.smallpic span{
    width: 200px;
    height: 200px;

    display: flex;
    flex-direction: column;
    align-items: center;
}
.smallpic .pdesc{

    width:200px;
    height: 100px;;

}
.smallpic .pdesc h6{
    color: #ccc;
}
.other{
    width: 1200px;
    margin: 0 auto;
    height: 700px;

}
