*{margin: 0;padding: 0}
.title{

    width: 1200px;
    height: 70px;
    margin: 0 auto;
}
.title h1{
    margin-top: 10px;
    float: left;
    margin-left:100px;
    color: grey;
}
.register{
    width: 1200px;
    height: 530px;
    border: 1px solid #ccc;
    margin: 0 auto;
}
.register .res01{
    float: left;
    width: 700px;
    height: 530px;

}
.register .res02{
    float: left;
    width: 490px;
    height: 450px;
    margin-top: 40px;
    border-left: 1px solid #cccc99;
}
.register .res01 div{
    width: 300px;
    margin-left:130px ;
    margin-top:10px ;
}
.register .res01 div span{
    font-size: 18px;
}
.register .res01 .username{
    margin-top: 100px;
}
.register .res01 .yzm{
    width: 150px;
}
.register .res01 .btn{
   padding-top: 20px;
    width: 311px;
}
.btn span a{
    font-size: 14px;
}
.btn input{
    margin: 0 8px;
}
.register .res02 h1{
    margin-top: 40px;
    font-size: 20px;
    font-weight: normal;
    margin-left: 30px;
    color: dimgrey;
}
.register .res02 h1 a{
    color: red;
}
.register .res02 span:nth-child(1){
    color: red;
    font-size: 55px;
}
.register .res02 span:nth-child(2){
    margin-left: 10px;
    color: green;
    font-size: 55px;
}