li{list-style: none}
a{text-decoration: none}
.top{width: 100%;height: 80px;position: fixed;top:0;background: #fff;z-index: 999;}
.toplist{
    margin: 0 auto;
    width:1200px;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.toplist .list_left img{
    width: 200px;
}


.toplist .list_center {


    width: 600px;
    border: 1px solid #ccc;
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 20px;


}
.toplist .list_center li{
    text-align: center;

    border-radius: 20px;
}
.place{
    position: fixed;
    width: 100%;
    height: 0px;
    z-index: 999999;
    left: 0px;
    /*transition: all 0.2s ease;*/
    top:60px;
    opacity: 0.9;
    display: none;

}
.place .placetop{
    width: 100%;
    height: 20px;
    background: #fff;
}
.place ul{float: left;}
.toplist .list_center li:nth-child(2):hover .place{
    display: block;
   height: 300px;
    background: #fffbe8;

}
.toplist .list_center li a{
    display: block;
    line-height: 40px;
    width: 120px;
    height: 40px;
    color: #696969;
    font-weight: bolder;
}
.toplist .list_center li:hover{
    background: #f76000;
}
.toplist .list_center li:hover a{
    color: #fff;
}

.toplist .list_right{
    margin-right: 20px;
}

.toplist .list_right span{
    margin: 0 5px;
    font-size: 12px;

}
.toplist .list_right span a{
    color: #1a1a1a;
}
.toplist .list_right span:first-child{
    cursor: pointer;
}

.login{
margin-top: 5px;

}

.empty{
    height: 20px;
}

