.container{
    margin-top: 80px;
}
.ant-carousel .slick-slide {
    text-align: center;
    height: 500px;

    background: #364d79;
    overflow: hidden;
}

.ant-carousel .slick-slide h3 {
    color: #fff;
}
.pic img{
    width: 100%;
    height: 600px;
}
.citypicture{
    text-align: center;
    width: 1200px;
    margin: 0 auto;
    height: 1200px;

}
.citypicturetitle{
    margin-top: 50px;
}
.citypicturetitle h1{
    color: red;
}
.citypicture .pictures{
    margin-top: 50px;
    height: 700px;

}
.ipt{margin: 0 auto;width: 600px}
.ipt ul{
   position: absolute;
    background: #fff;
    width: 600px;

}
.pic01 img{
    padding: 8px;
    transition:all 1s ease;
}
.pic01 img:hover{
    box-shadow: 0px 0px 5px #888888;
    border-color:#ccc ;
}
.person{
    text-align: center;
    width:100%;
    background: #00B7FF;
}
.personin{
    width: 1300px;
    height: 650px;
    margin: 0 auto;


}
.person .person01 h1{color: #fff}
.person .person01{
    color: #fff;
    padding-top: 50px;

}
.person .person02{
    margin-top: 50px;

}
.person .person02 .person03{
    width: 1300px;
    margin: 0 auto;

}
.person .person02 .person03 img{

    border: 10px solid #00B7FF;
    transition:all 1s ease;
}
.person .person02 .person03 img:hover{
    border-color:#fff ;
}
.feel{
    text-align: center;
    margin: 0 auto;
    width: 1200px;

    border: 1px solid #ccc;
}
.feel .feel01{
    padding-top: 50px;
}
.feel .feel01 h1{
    color: red;
}
.feel .feel02{
    position: relative;
    height: 900px;
    border: 1px solid #ccc;
}
.feel .feel02 .pic03{

    width: 296px;
    float: left;
    border: 15px solid #fff;
    transition: all 1s ease;
    border-radius: 10px;

}
.feel .feel02 .pic03:hover{
    border-color:#ccc;
}

.feel .feel02 .pic03 img{
    width: 100%;
    height: 100%;
}
.feel .feel02 .pic03:last-child{
    position: absolute;
    left: 592px;
    top: 448px;
}
.foot{
    text-align: center;
    position: relative;
    width: 1200px;
    height: 700px;
    border: 1px solid #ccc;
    margin: 0 auto;
}
.foot img{
    width: 100%;
    height: 100%;
    margin-left: 50px;
}
.foot .foot01{
    width: 1200px;
    height: 180px;
    border: 1px solid #ccc;
}
.foot .foot01 h1 i{
    display: inline-block;
    width: 50px;
    height: 50px;
    border-radius: 8px;
    color: #fff;
    background: red;
   line-height: 50px;
    margin: 0 10px;
}
.foot .foot01 h1 i span{
    font-size: 40px;
}
.foot .foot01 h1{
    font-size: 40px;
}
.foot .foot01 h1{
    margin-bottom: -3px;
}
.foot .foot01 p:nth-of-type(1){
    font-size: 11px;
    margin-right: -50px;
}
.foot .foot01 p span{
font-size: 50px;
    margin: 0 50px;
}
.foot .foot01 p span:nth-child(1){
    color: red;
}
.foot .foot01 p span:nth-child(2){
    color: green;
}
.foot .foot01 p span:nth-child(2):hover{

}
.foot .foot02 li{
    float: left;
    padding: 0 10px;
    border-right: 1px solid #000;

}
.foot .foot02 li:last-child{
    border-right: none;
}